:root {
  --index: calc(1vw + 1vh);
  --c-primary: #13212d;
  --c-gold: #d5b378;
  --c-gold-hover: #815e21;
  --main-font: "Century Gothic", sans-serif;
}

body,
html {
  &.noskroll {
    overflow: hidden;
  }
}

html {
  width: 100%;
  font-size: 62.5%;
  height: fill-available;
  height: -webkit-fill-available;
  scroll-behavior: smooth;
}

body {
  position: relative;
  min-height: fill-available;
  min-height: -webkit-fill-available;
  min-width: 320px;
  color: #eee;
  font-family: var(--main-font);
  font-size: 1.6rem;
  line-height: normal;
  font-weight: 400;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--c-primary);
}

img,
svg {
  max-width: 100%;
  max-height: 100%;
}

strong {
  font-weight: 700;
}

canvas {
  display: block;
}

.custom-scrollbar {
  scrollbar-width: thin;
  scrollbar-color: var(--c-primary) var(--c-gold);
  -ms-overflow-style: -ms-autohiding-scrollbar;

  &::-webkit-scrollbar {
    width: 8px;
    height: 6px;
    background: var(--c-primary);
  }
  &::-webkit-scrollbar-thumb {
    background-color: var(--c-gold);
  }
}

.page {
  max-width: 260rem;
  margin: 0 auto;
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1260px;
  margin: 0 auto;
  position: relative;
  padding: 0 20px;

  &.is-full-w {
    max-width: 1440px;
  }
}

ul li {
  list-style-type: decimal;
}

ol li {
  list-style-type: disc;
}

a.text-link {
  transition: 0.3s;

  &:hover {
    color: var(--c-gold);
  }
}

.is-hidden {
  display: none !important;
}

.fit-cover {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

img {
  &.initial {
    max-width: initial;
    max-height: initial;
  }
}

.button {
  font-weight: 700;
  border-radius: 15px;
  padding: 0 3.2rem;
  transition: 0.3s;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  cursor: pointer;
  min-width: 20rem;

  &.is-primary {
    color: #111111;
    background: var(--c-gold);
    border: none;

    &:hover {
      background: var(--c-gold-hover);
      color: #ffffff;
    }
  }

  &.is-ghost {
    border: 2px solid var(--c-gold);
    color: var(--c-gold);
    background: none;

    &:hover {
      border-color: var(--c-gold-hover);
      color: var(--c-gold-hover);
    }
  }

  &.is-large {
    height: 6.9rem;
    font-size: 2.4rem;

    @media screen and (max-width: $sm) {
      font-size: 1.6rem;
      height: 6rem;
    }
  }

  &.is-medium {
    height: 5.6rem;
    font-size: 2rem;

    @media screen and (max-width: $sm) {
      font-size: 1.6rem;
      height: 5.2rem;
    }
  }

  &.is-small {
    height: 4.4rem;
    font-size: 1.6rem;
  }

  &.is-center {
    margin: 0 auto;
    display: flex;
    width: max-content;
  }
}

h1,
.h1 {
  font-size: 4rem;
  font-weight: 700;

  @media screen and (max-width: $md) {
    font-size: 2.4rem;
  }

  @media screen and (max-width: $xs) {
    font-size: 2rem;
  }
}

h2,
.h2 {
  font-size: 2.4rem;
  font-weight: 700;

  @media screen and (max-width: $md) {
    font-size: 2.6rem;
  }

  @media screen and (max-width: $sm) {
    font-size: 2rem;
  }

  @media screen and (max-width: $xs) {
    font-size: 1.6rem;
  }
}

h3,
.h3 {
  font-size: 2rem;
  font-weight: 700;

  @media screen and (max-width: $md) {
    font-size: 1.6rem;
  }
}

h4,
.h4 {
  font-size: 2.4rem;
  font-weight: 400;

  @media screen and (max-width: $md) {
    font-size: 2.6rem;
  }

  @media screen and (max-width: $sm) {
    font-size: 1.6rem;
  }
}

.page-section {
  margin-bottom: 10rem;

  @media screen and (max-width: $md) {
    margin-bottom: 6rem;
  }

  &__title-box {
    display: flex;
    flex-direction: column;
    margin-bottom: 6.4rem;
    gap: 3.2rem;

    @media screen and (max-width: $md) {
      margin-bottom: 3.2rem;

      br {
        display: none;
      }
    }

    &.is-center {
      justify-content: center;
      text-align: center;
    }
  }
}

.form-field-box {
  display: flex;
  flex-direction: column;
  width: 100%;
  position: relative;
  overflow: hidden;
  isolation: isolate;

  &__placeholder {
    color: #111111;
    margin-bottom: 0.8rem;

    &.is-white {
      color: #ffffff;
    }
  }
}

.input {
  background: none;
  border: 1px solid #111;
  border-radius: 15px;
  height: 4.4rem;
  padding: 0 2rem;
  transition: 0.3s;
  color: #111111;
  position: relative;
  z-index: 2;
  transition: 0.2s;

  &::placeholder {
    color: #111111;
  }

  &.is-error {
    color: #d80000 !important;
    border-color: #d80000 !important;
  }

  &.is-white {
    border-color: #ffffff;
    color: #ffffff;

    &::placeholder {
      color: #ffffff;
    }
  }

  &:focus {
    border-color: var(--c-gold) !important;
  }
}

.textarea {
  padding: 1.6rem 2.4rem;
  height: 14rem;
  resize: none;
}

.checkbox-input {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;

  label {
    display: flex;
    align-items: center;
    gap: 8px;
    cursor: pointer;
  }

  /* &__check {
    width: 20px;
    height: 20px;
    background: #12171e;
    border-radius: 4px;
    flex-shrink: 0;
    transition: 0.2s;
    display: flex;
