  &__radio {
    width: 20px;
    height: 20px;
    background: none;
    border-radius: 50%;
    flex-shrink: 0;
    transition: 0.2s;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid var(--c-gold);
    background: none;

    &::before {
      content: "";
      display: block;
      width: 10px;
      height: 10px;
      border-radius: 50%;
      background: var(--c-gold);
      opacity: 0;
      transition: 0.2s;
    }
  }

  &__placeholder {
    font-size: 1.4rem;
    color: #111111;

    a {
      text-decoration: underline;
    }
  }

  input {
    display: none;

    /*     &:checked + .checkbox-input__check {
      &::before {
        opacity: 1;
      }

      .checkbox-input__check-icon {
        opacity: 1;
      }
    } */

    &:checked + .checkbox-input__radio {
      &::before {
        opacity: 1;
      }
    }
  }
}

.field-error {
  color: #eb5757;
  padding-top: 6px;
  display: none;

  &.is-visible {
    display: block;
  }
}

.tab-content {
  opacity: 0;
  visibility: hidden;
  position: absolute;

  &.is-visible {
    opacity: 1;
    visibility: visible;
    position: unset;
  }
}

.swiper-slider {
  &__arrow {
    position: absolute;
    top: 50%;
    transform: translate(0, -50%);
    cursor: pointer;
    z-index: 50;

    path {
      transition: 0.3s;
    }

    &:hover {
      path {
        stroke: var(--c-gold-hover);
      }
    }

    &.is-prev {
      left: 0;
    }

    &.is-next {
      right: 0;
    }
  }
}

