.swiper-pagination {
  position: unset;
  padding-top: 3.2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.6rem;
}

.swiper-pagination-bullet {
  background: #ffffff;
  opacity: 1;
  margin: 0 !important;

  &.swiper-pagination-bullet-active {
    background: var(--c-gold-hover);
  }
}

.map-block {
  .is-gray {
    filter: invert(100%);
  }

  .ymaps-2-1-79-copyrights-pane {
    display: none !important;
  }
}

.start-banner {
  background: url(../img/first-screen.jpg) center/cover no-repeat;
  height: 100vh;
  display: flex;
  align-items: center;
  position: relative;

  @media screen and (max-width: $sm) {
    text-align: center;
  }

  &__title {
    margin-bottom: 2.4rem;
    max-width: 69rem;
  }

  &__subtitle {
    margin-bottom: 4.8rem;
    max-width: 72rem;
  }

  .button {
    @media screen and (max-width: $sm) {
      width: 100%;
    }
  }

  &__to-content {
    background: var(--c-gold);
    position: absolute;
    bottom: 5rem;
    left: 50%;
    transform: translate(-50%, 0);
    width: 7rem;
    height: 7rem;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    animation: startBannerArrow 1s ease-in-out infinite;
    transition: 0.3s;

    @media screen and (max-width: $sm) {
      width: 5.4rem;
      height: 5.4rem;

      svg {
        width: 17px;
      }
    }

    &:hover {
      background: var(--c-gold-hover);
    }
  }
}

@keyframes startBannerArrow {
  0% {
    transform: translate(-50%, 0);
  }
  50% {
    transform: translate(-50%, 10px);
  }
  100% {
    transform: translate(-50%, 0);
  }
}

.numbers-section {
  @media screen and (min-width: $lg) {
    .swiper-wrapper {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      gap: 6.4rem 11.2rem;
    }

    .swiper-slide {
      width: max-content;
    }
  }

  &__item {
    position: relative;
    overflow: hidden;
    padding: 2rem;

    @media screen and (min-width: $lg) {
      &:hover {
        .numbers-section__item-bg .is-hover {
          opacity: 1;
        }

        .numbers-section__item-content {
          bottom: 100%;
          transform: translate(0, 120%);
        }
      }
    }

    @media screen and (max-width: $lg) {
      width: max-content;
      margin: 0 auto;
    }
  }

  &__item-bg {
    .is-hover {
      position: absolute;
      top: 2rem;
      left: 2rem;
      max-width: initial;
      max-height: initial;
      opacity: 0;
      transition: 0.3s ease-in-out;

      @media screen and (max-width: $lg) {
        opacity: 1;
      }
    }
  }

  &__item-content {
    position: absolute;
    bottom: 0;
    text-align: center;
    transition: 0.3s ease-in-out;

    @media screen and (max-width: $lg) {
      bottom: 50%;
      transform: translate(0, 50%);
    }
  }

  &__item-title {
    color: var(--c-gold);

    @media screen and (max-width: $lg) {
      margin-bottom: 1.6rem;
    }
  }

  &__item-text {
    @media screen and (min-width: $lg) {
      position: absolute;
      top: calc(100% + 1.6rem);
    }
  }
}

.about-section {
  &__wrapper {
    display: flex;
    flex-direction: column;
  }

  &__row {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0 4rem;

    @media screen and (min-width: $lg) {
      &:nth-child(even) {
        flex-direction: row-reverse;
      }
    }

    @media screen and (max-width: $lg) {
      flex-direction: column;
    }
  }

  &__row-image {
    flex-shrink: 0;
    max-width: 720px;
  }

  &__row-content {
    @media screen and (min-width: $lg) {
      max-width: 56rem;
    }
  }

  &__row-title {
    margin-bottom: 1.6rem;
  }
}

.wood-tent {
  margin: 0 auto;
  border-radius: 30px;
  overflow: hidden;
  box-shadow: 10px 10px 25px 0px #10161c;
  max-width: 992px;
  width: 100%;
  aspect-ratio: 992 / 575;
  position: relative;

  @media screen and (max-width: $lg) {
    border-radius: 15px;
  }

  &__content {
    width: 100%;
    padding: 3.2rem 6.4rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 2rem;
    background: rgba(13, 19, 24, 0.5);
    backdrop-filter: blur(7.5px);
    position: absolute;
    left: 0;
    bottom: 0;

    @media screen and (max-width: $lg) {
      padding: 0.8rem 1.6rem;
    }
  }

  &__content-title {
    @media screen and (max-width: $md) {
      font-size: 1.6rem !important;
    }
  }

  &__content-adress {
    font-weight: 400;
    font-size: 2rem;
    padding-top: 1.6rem;

    @media screen and (max-width: $md) {
      padding-top: 0.8rem;
      font-size: 1.4rem;
    }
  }
}

.tent-detail-info {
  display: flex;
  flex-direction: column;
  gap: 3.2rem;

  &__row {
    display: flex;
    align-items: center;
    gap: 3.2rem;

    &:nth-child(odd) {
      .tent-detail-info__image {
        @media screen and (min-width: $lg) {
          width: calc(50% - 3.2rem);
        }
      }
    }

    &:nth-child(even) {
      flex-direction: row-reverse;
    }

    @media screen and (max-width: $lg) {
      flex-direction: column !important;
    }
  }

  &__image {
    flex-shrink: 0;
    border-radius: 15px;
    overflow: hidden;

    @media screen and (min-width: $lg) {
      width: 50%;
    }
  }

  &__content {
    font-size: 1.6rem;
  }
}

.tent-advantages {
  margin: 0 auto;
  position: relative;
  width: max-content;
  max-width: 100%;

  &__wrapper {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    @media screen and (max-width: 500px) {
      top: -15px;
      left: -30px;
    }
  }

  &__message {
    padding: 1.8rem;
    width: 300px;
    position: absolute;

    @media screen and (min-width: 1200px) {
      z-index: 50;
    }

    &.is-1 {
      left: 13%;
      top: 45%;
    }

    &.is-2 {
      left: 40%;
      top: 25%;
    }

    &.is-3 {
      left: 57%;
      top: 8%;
    }

    &.is-4 {
      left: 77%;
      top: 50%;
    }

    &.is-5 {
      left: 45%;
      top: 80%;
    }

    .is-desktop {
      @media screen and (max-width: 1200px) {
        .tent-advantages__message-title,
        .tent-advantages__message-description {
          display: none;
        }
      }
    }

    .is-mobile {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 9999;
      background: rgba(23, 23, 23, 0.8);
      display: flex;
      align-items: center;
      justify-content: center;
      opacity: 0;
      visibility: hidden;
      transition: 0.3s;

      @media screen and (min-width: 1200px) {
        display: none;
      }

      .tent-advantages__message-title,
      .tent-advantages__message-description {
        display: block;
        opacity: 1;
      }
    }

    .tent-advantages__is-mobile-content {
      display: block;
      background: #ffffff;
      border-radius: 15px;
      width: 100%;
      max-width: 300px;
      padding: 1.6rem;
      position: relative;
    }

    .tent-advantages__is-mobile-content-close {
      position: absolute;
      right: 0;
      top: -10px;
      width: 40px;
    }

    &::before {
      content: "";
      display: block;
      background: #ffffff;
      border-radius: 15px;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      transition: 0.3s;
      transform: scale(0.2);
      transform-origin: left top;
    }

    &.is-active {
      cursor: pointer;

      @media screen and (min-width: 1200px) {
        &::before {
          transform: scale(1);
          opacity: 1;
        }
        .tent-advantages__message-title {
          opacity: 1;
          visibility: visible;
          width: auto;
        }

        .tent-advantages__message-description {
          opacity: 1;
          visibility: visible;
          width: auto;
        }
      }

      @media screen and (max-width: 1200px) {
        .is-mobile {
          opacity: 1;
          visibility: visible;

          .tent-advantages__message-title {
            width: calc(100% - 20px);
          }

          .tent-advantages__message-title,
          .tent-advantages__message-description {
            visibility: visible;
          }
        }
      }

      .tent-advantages__message-point {
        &::after {
          width: 80px;
          height: 80px;
          opacity: 0;
          visibility: hidden;
        }
      }
    }
  }

  &__message-point {
    width: 18px;
    height: 18px;
    position: relative;
    border: 2px solid var(--c-gold);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    cursor: pointer;

    &::before {
      content: "";
      display: block;
      background: var(--c-gold);
      width: 12px;
      height: 12px;
      border-radius: 50%;
    }

    &::after {
      content: "";
      display: block;
      border: 2px solid var(--c-gold);
      width: 40px;
      height: 40px;
      border-radius: 50%;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      transition: 0.3s;
    }

    &:hover {
      &::after {
        width: 54px;
        height: 54px;
      }
    }
  }

  &__message-header {
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-bottom: 1.2rem;
  }

  &__message-title {
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s;
    color: #111111;
    position: relative;
    z-index: 5;
  }

  &__message-description {
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s;
    color: #111111;
    position: relative;
    z-index: 5;
    font-size: 1.4rem;
  }
}

.tents-slider {
  @media screen and (max-width: $lg) {
    padding: 0 20px;
  }

  .swiper-slide {
    @media screen and (min-width: $lg) {
      width: 736px;
    }
  }

  .swiper-wrapper {
    height: 425px;

    @media screen and (max-width: $lg) {
      height: 325px;
    }

    @media screen and (max-width: $sm) {
      height: 225px;
    }
  }
}

.about-card {
  height: 100%;
  border-radius: 30px;
  position: relative;
  overflow: hidden;
  box-shadow: 10px 10px 25px 0px #10161c;

  @media screen and (max-width: $lg) {
    border-radius: 15px;
  }

  &__image {
    height: 100%;
  }

  &__content {
    width: 100%;
    padding: 3.2rem 6.4rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 2rem;
    background: rgba(13, 19, 24, 0.5);
    backdrop-filter: blur(7.5px);
    position: absolute;
    left: 0;
    bottom: 0;
    text-align: left;

    @media screen and (max-width: $lg) {
      padding: 0.8rem 1.6rem;
    }
  }

  &__content-title {
    max-width: 30rem;

    @media screen and (max-width: $md) {
      font-size: 1.6rem !important;
    }
  }

  &__content-adress {
    font-weight: 400;
    padding-top: 0.8rem;
    max-width: 30rem;

    @media screen and (max-width: $md) {
      padding-top: 0.8rem;
      font-size: 1.4rem;
    }
  }
}

.tent-detail-card {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 6.4rem;
  padding: 6rem 4rem;

  @media screen and (max-width: $lg) {
    padding: 6rem 0;
  }

  @media screen and (max-width: $md) {
    grid-template-columns: repeat(1, 1fr);
    gap: 3.2rem;
    padding: 0;
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }

  &__content-box {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  &__slider {
    width: 100%;
    overflow: hidden;

    .swiper.is-main {
      aspect-ratio: 1 / 1;
      margin-bottom: 32px;

      @media screen and (max-width: $md) {
        aspect-ratio: initial;
        margin-bottom: 0;
      }

      .swiper-slide {
        overflow: hidden;
        border-radius: 15px;

        @media screen and (max-width: $md) {
          height: 325px;
        }

        @media screen and (max-width: $sm) {
          height: 200px;
        }
      }
    }

    .swiper.is-nav {
      @media screen and (max-width: $md) {
        display: none;
      }
      .swiper-slide {
        overflow: hidden;
        border-radius: 15px;
        aspect-ratio: 1 / 1;
        border: 2px solid transparent;
        cursor: pointer;

        &.swiper-slide-thumb-active {
          border-color: var(--c-gold);
        }
      }
    }
  }
}

.consultation-form {
  display: flex;
  background: #ffffff;
  border-radius: 30px;
  box-shadow: 10px 10px 25px 0px #10161c;
  padding: 4.8rem;
  max-width: 992px;
  width: 100%;
  margin: 0 auto;
  position: relative;

  @media screen and (min-width: $lg) {
    display: flex;
    justify-content: flex-end;
    min-height: 60rem;
  }

  @media screen and (max-width: $sm) {
    padding: 3.2rem;
  }

  &__title {
    color: #111111;

    @media screen and (max-width: $sm) {
      text-align: center;
    }
  }

  &__img {
    flex-shrink: 0;
    width: 50%;
    position: absolute;
    left: 0;
    bottom: -30px;

    @media screen and (max-width: $lg) {
      display: none;
    }
  }

  &__content {
    width: 53%;
    position: relative;
    z-index: 10;

    @media screen and (max-width: $lg) {
      width: 100%;
    }
  }

  &__form {
    max-width: 32rem;
    padding-top: 3.2rem;
    display: flex;
    flex-direction: column;
    gap: 1.6rem;

    @media screen and (max-width: $lg) {
      max-width: 100%;
    }

    .button {
      @media screen and (min-width: $sm) {
        max-width: 20rem;
      }

      margin-top: 1.6rem;
    }
  }
}

.constructions {
  &__tabs {
    position: relative;
    overflow: hidden;
    display: flex;
    margin: 0 auto 6.4rem;

    @media screen and (max-width: $lg) {
      margin: 0 auto 3.2rem;
    }
  }

  &__tab {
    display: flex;
    cursor: pointer;
    background: #1e2b37;
    transition: 0.3s;
    border-radius: 30px;
    width: 100%;
    padding: 3.2rem;
    cursor: pointer;

    @media screen and (max-width: $lg) {
      padding: 1rem;
    }

    &.is-quadrangular {
      border-radius: 30px 0 0 30px;
      clip-path: polygon(0 0, 60% 0, 100% 100%, 0% 100%);
      width: 50%;
      justify-content: center;

      @media screen and (max-width: $sm) {
        width: 55%;
        border-radius: 15px 0 0 15px;
      }

      .constructions__tab-content {
        max-width: 285px;
        transform: translate(-30%, 0);

        @media screen and (max-width: $sm) {
          transform: translate(-15%, 0);
        }
      }
    }

    &.is-hexoganal {
      justify-content: center;
      border-radius: 0 30px 30px 0;
      clip-path: polygon(0 0, 100% 0, 100% 100%, 30% 100%);
      position: absolute;
      right: 0;
      top: 0;
      width: calc(70% - 30px);

      .constructions__tab-content {
        transform: translate(25%, 0);
      }

      @media screen and (max-width: $sm) {
        width: calc(70% - 10px);
        border-radius: 0 15px 15px 0;

        .constructions__tab-content {
          transform: translate(15%, 0);
        }
      }
    }

    &.is-active {
      background: var(--c-gold);

      img.is-active {
        opacity: 1;
      }

      .constructions__tab-title,
      .constructions__tab-text {
        color: #111111;
      }
    }
  }

  &__tab-content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__tab-image {
    position: relative;
    margin-bottom: 3.2rem;

    @media screen and (max-width: $md) {
      height: 46px;
      margin-bottom: 1rem;
    }

    img.is-active {
      position: absolute;
      left: 0;
      top: 0;
      opacity: 0;
      transition: 0.3s;
    }
  }

  &__tab-title {
    color: #ffffff;
    text-align: center;
    margin-bottom: 1.6rem;
    transition: 0.3s;

    @media screen and (max-width: $md) {
      font-size: 1.4rem !important;
      margin-bottom: 0;
    }
  }

  &__tab-text {
    text-align: center;
    transition: 0.3s;
  }

  &__content {
    position: relative;
  }

  &__content-tab {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2.7rem;
    margin: 0 auto;

    opacity: 0;
    visibility: hidden;
    position: absolute;

    &.is-visible {
      opacity: 1;
      visibility: visible;
      position: unset;
    }

    @media screen and (max-width: $lg) {
      display: flex;
      gap: 3.27rem;
      overflow: auto;
      padding: 0 6rem;
      margin: 0 -2rem;
    }
  }
}

.constructions-card {
