.footer {
  &__wrapper {
    display: grid;
    grid-template-columns: 58% 42%;
    background: #1e2b37;

    @media screen and (max-width: $lg) {
      display: block;
    }
  }

  &__content {
    padding: 6.4rem;
    display: flex;
    flex-direction: column;
    gap: 6.4rem;

    @media screen and (max-width: $lg) {
      padding: 3.2rem 2rem;
      gap: 3.2rem;
    }

    @media screen and (max-width: $sm) {
      text-align: center;

      .h1 br {
        display: none;
      }
    }
  }

  &__content-box {
    display: flex;
    flex-direction: column;
    gap: 3.2rem;
  }

  &__form {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 3.2rem;
    max-width: 32rem;

    @media screen and (max-width: $sm) {
      gap: 1.6rem;
      max-width: 100%;
      width: 100%;
    }

    .button {
      width: 157px;
      font-weight: 400;
      padding: 0;
      min-width: initial;

      @media screen and (max-width: $sm) {
        width: 100%;
      }
    }
  }

  &__form-messengers {
    display: flex;
    align-items: center;
    gap: 3rem;

    @media screen and (max-width: $sm) {
      justify-content: center;
      width: 100%;
    }
  }

  &__form-messenger {
    cursor: pointer;

    svg {
      path,
      rect {
        transition: 0.3s;
      }
    }

    &:hover {
      svg {
        &.is-viber {
          path {
            fill: var(--c-gold);
          }
        }
        &.is-whatsapp {
          path {
            fill: var(--c-gold);
          }
        }

        &.is-telegram {
          path {
            stroke: var(--c-gold);
          }
        }

        &.is-phone {
          path {
            stroke: var(--c-gold);
          }

          rect {
            stroke: var(--c-gold);
          }
        }
      }
    }

    input {
      display: none;

      &:checked {
        & + .footer__form-messenger-icon {
          svg {
            &.is-viber {
              path {
                fill: var(--c-gold-hover);
              }
            }
            &.is-whatsapp {
              path {
                fill: var(--c-gold-hover);
              }
            }

            &.is-telegram {
              path {
                stroke: var(--c-gold-hover);
              }
            }

            &.is-phone {
              path {
                stroke: var(--c-gold-hover);
              }

              rect {
                stroke: var(--c-gold-hover);
              }
            }
          }
        }
      }
    }
  }

  &__middle {
    background: #13212d;
    padding: 3rem 0;

    @media screen and (max-width: $lg) {
      padding: 1.2rem 0;

      .header__wrapper {
        justify-content: center;
      }
    }

    @media screen and (max-width: $sm) {
      .header__socials {
        justify-content: space-around;
        width: 100%;
      }
    }
  }

  &__bottom {
    background: #111111;
    text-align: center;
    padding: 2rem 0;
    font-weight: 700;

    @media screen and (max-width: $sm) {
      font-size: 1.4rem;
      font-weight: 400;
      padding: 2.4rem 2rem;
    }
  }
}

.footer-map {
  height: 100%;

  .is-gray {
    filter: grayscale(1);
  }
}

.to-top-btn {
  opacity: 0;
  visibility: hidden;
  position: fixed;
  right: 6.4rem;
  bottom: 6.4rem;
  cursor: pointer;
  background: var(--c-gold);
  transition: 0.3s;
  width: 7rem;
  height: 7rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 50;

  @media screen and (max-width: $lg) {
    width: 5.4rem;
    height: 5.4rem;

    svg {
      width: 17px;
    }
  }

  @media screen and (max-width: $md) {
    display: none;
  }

  &.is-visible {
    opacity: 1;
    visibility: visible;
  }

  &:hover {
    background: var(--c-gold-hover);
  }
}
