.header {
  height: 10rem;
  display: flex;
  align-items: center;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 300;
  width: 100%;
  background: var(--c-primary);

  @media screen and (max-width: $lg) {
    height: 7.4rem;
  }

  .container {
    @media screen and (min-width: $xl) {
      padding: 0 6.4rem;
    }
  }

  &__wrapper {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__nav {
    display: flex;
    gap: 6rem;

    @media screen and (max-width: $xl) {
      gap: 2rem;
    }

    @media screen and (max-width: $lg) {
      flex-direction: column;
      gap: 6.4rem;
    }
  }

  &__nav-wrapper {
    @media screen and (max-width: $lg) {
      position: fixed;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background: var(--c-primary);
      z-index: 50;
      padding: 7.4rem 4.8rem 4rem;
      overflow: auto;

      opacity: 0;
      visibility: hidden;
      transition: 0.3s;

      &.is-active {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  &__nav-wrapper-cross {
    position: absolute;
    top: 8px;
    right: 20px;
  }

  &__nav-item {
    font-size: 2.4rem;
    font-weight: 400;
  }

  &__nav-wrapper-cross {
    cursor: pointer;
  }

  &__phone {
    font-size: 2rem;
    font-weight: 700;
  }

  &__socials {
    display: flex;
    align-items: center;
    gap: 3rem;

    svg {
      path,
      rect {
        transition: 0.3s;
      }
    }

    a:hover {
      svg:not(.is-youtube) {
        path {
          fill: var(--c-gold);
        }
      }
      svg.is-youtube {
        path {
          stroke: var(--c-gold);
        }
      }
    }

    a:focus {
      svg:not(.is-youtube) {
        path {
          fill: var(--c-gold-hover);
        }
      }
      svg.is-youtube {
        path {
          stroke: var(--c-gold-hover);
        }
      }
    }
  }
}
