.popup {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 9999;
  background: rgba(23, 23, 23, 0.8);
  padding: 80px 20px;
  overflow-y: auto;
  opacity: 0;
  visibility: hidden;
  transition: 0.5s;

  &.is-visible {
    opacity: 1;
    visibility: visible;
  }

  @media screen and (max-width: $sm) {
    padding: 20px;
  }

  &.is-center {
    @media screen and (min-width: 500px) {
      .popup__wrapper {
        position: absolute;
        top: 50%;
        left: 20px;
        right: 20px;
        transform: translateY(-50%);
      }
    }
  }

  &__wrapper {
    position: relative;
    width: 100%;
    max-width: 1000px;
    margin: 0 auto;
    background: var(--c-primary);
    border-radius: 30px;
    box-shadow: 0px 0px 50px 0px rgba(0, 0, 0, 0.7);
    overflow: hidden;
    padding: 3.2rem 6.4rem;

    @media screen and (max-width: $lg) {
      padding: 5rem 2rem 2rem;
    }

    @media screen and (max-width: $md) {
      padding: 2.4rem 2rem;
    }

    &.is-small {
      background: #eee;
      max-width: 500px;
      padding: 3.2rem 9rem;

      @media screen and (max-width: $sm) {
        padding: 4.8rem 3.2rem;
      }

      .popup__close {
        background: none;

        path {
          stroke: #111111;
        }
      }
    }
  }

  &__close {
    width: 54px;
    height: 54px;
    background: var(--c-primary);
    border-radius: 50%;
    position: absolute;
    top: 5px;
    right: 5px;
    z-index: 50;
    cursor: pointer;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__title {
    color: #ffffff;
    flex-shrink: 0;
    text-align: center;
    margin: 0 auto 4rem;
    width: calc(100% - 10rem);
  }
}
